import React, { useState, useEffect } from 'react';
import Header from '../header';
import { FaShareAlt, FaInstagram, FaLinkedin, FaYoutube, FaTwitter } from "react-icons/fa";
import { GrClose } from "react-icons/gr";
import './social.scss';

function SocialLinks(props) {
    const [openMenu, setOpenMenu] = useState(false);
    function toggleMenu(){
        setOpenMenu(!openMenu);
    }
    useEffect(()=>{     
    },[]);  
    return (
        <>
            <Header number={props.number} />
            <div className="App-container" style={{'backgroundColor':'#ecf0f1', 'minHeight':'calc(100vh - 30px)', 'display':'flex', 'alignItems': 'center', 'justifyContent':'center'}}>
                <div className="social-media-container">
                    <div className={`social-media-list${(openMenu)?' show':''}`}>
                        <div className="share-btn" onClick={toggleMenu}><FaShareAlt size={30} /></div>
                        <a href="https://www.instagram.com/mackralg/" target="_blank" rel="noreferrer" className="social-media-item"><FaInstagram size={30} color='#dd2a7b' /></a>
                        <a href="https://www.youtube.com/" target="_blank" rel="noreferrer" className="social-media-item"><FaYoutube size={30} fill='#ff0000' /></a>
                        <div className="social-media-item" onClick={toggleMenu}><GrClose size={30 } color='#282c37' /></div>
                        <a href="https://www.linkedin.com/in/mackral/" target="_blank" rel="noreferrer" className="social-media-item"><FaLinkedin size={30} color='#007bb5' /></a>
                        <a href="https://twitter.com/i_am_mackral" target="_blank" rel="noreferrer" className="social-media-item"><FaTwitter size={30} color='#1da1f2' /></a>
                    </div>
                </div>
            </div>
        </>
    );
}

export default SocialLinks;
