import React, { useState } from 'react';
import { FaWindowClose, FaInstagram, FaLinkedin, FaYoutube, FaTwitter, FaMailBulk } from "react-icons/fa";
function Footer() {
  const [openSocial, setOpenSocial] = useState(false);
  function toggleMenu(){
    setOpenSocial(!openSocial);
  }
  function closeMenu(){
    setOpenSocial(false);
  }
  return (
    <>
      <footer className="App-footer">
        <div>Part of <span className="hashtag">#100Days100Projects</span></div>
      </footer>
      <div className="footer-get-in-touch" onClick={toggleMenu}><FaMailBulk size={30} /><span>Get in touch</span></div>
      <div className={`footer-social-panel${(openSocial)?' show':''}`}>
        <div className="footer-social-panel-header-container">
          <div></div>
          <div className="footer-social-panel-header">Created with <span className="heart">&hearts;</span> by <a href="https://mackralg.github.io/mackralg/#/" target="_blank" rel="noreferrer">Mac</a></div>
          <FaWindowClose className="footer-social-panel-close" size={25} onClick={closeMenu} />
        </div>
        <h4 className="footer-social-panel-sub-header">Get in touch on</h4>
        <div className="footer-social-panel-link">
          <a href="https://www.instagram.com/mackralg/" target="_blank" rel="noreferrer" className="footer-social-panel-item"><FaInstagram size={30} color='#dd2a7b' /></a>
          <a href="https://www.youtube.com/" target="_blank" rel="noreferrer" className="footer-social-panel-item"><FaYoutube size={30} fill='#ff0000' /></a>
          <a href="https://www.linkedin.com/in/mackral/" target="_blank" rel="noreferrer" className="footer-social-panel-item"><FaLinkedin size={30} color='#007bb5' /></a>
          <a href="https://twitter.com/i_am_mackral" target="_blank" rel="noreferrer" className="footer-social-panel-item"><FaTwitter size={30} color='#1da1f2' /></a>
        </div>
      </div>
    </>
  );
}

export default Footer;
