import React, { useState } from 'react';
import Header from '../header';
import { ImQuotesLeft } from "react-icons/im";
import styles from './testimonial.module.scss';

function Testimonial(props) {
    let [activeTest, setActiveTest] = useState(0);
    
    function updateActive(e){
        setActiveTest(e);
    } 
    const testimonials = [{
        text: 'Your company is truly upstanding and is behind its product 100%. I am completely blown away. I like developer more and more each day because it makes my life a lot easier."',
        name: 'Arthur Birnbaum',
        color: 'rgba(235, 59, 90,1.0)',
        img : 'https://randomuser.me/api/portraits/men/57.jpg'
    },{
        text: 'Thank You! Developer should be nominated for service of the year. Without developer, we would have gone bankrupt by now. I would also like to say thank you to all your staff.',
        name: 'Phoebe Kotliar',
        color: 'rgba(250, 130, 49,1.0)',
        img : 'https://randomuser.me/api/portraits/women/50.jpg'
    },{
        text: "You've saved our business! Buy this now. Thank You! Developer impressed me on multiple levels.",
        name: 'Louisa P. Huard',
        color: 'rgba(75, 123, 236,1.0)',
        img : 'https://randomuser.me/api/portraits/men/80.jpg'
    },{
        text: 'I was amazed at the quality of developer. Since I invested in developer I made over 100,000 dollars profits. Man, this thing is getting better and better as I learn more about it.',
        name: 'Cristina Aurmoogum',
        color: 'rgba(165, 94, 234,1.0)',
        img : 'https://randomuser.me/api/portraits/women/30.jpg'
    },{
        text: "I don't always clop, but when I do, it's because of developer. Wow what great service, I love it!",
        name: 'J. Kent Pepper',
        color: 'rgba(32, 191, 107,1.0)',
        img : 'https://randomuser.me/api/portraits/men/77.jpg'
    }]; 
    return (
        <>
            <Header number={props.number} />
            <div className={styles.App_container}>
                <div className={styles['testimonial-container']} style={{backgroundColor: testimonials[activeTest]['color']}}>
                    <div className={styles['testimonial-text']}>
                        <div className={styles["testimonial-quote"]}><ImQuotesLeft size={60} /></div>
                        {testimonials[activeTest]['text']}
                    </div>
                    <div className={styles["testimonial-name"]}>{testimonials[activeTest]['name']}</div>
                    <div className={styles["testimonial-index"]}>
                        {
                            testimonials.map((item, i)=>
                                <div key={i} className={`${styles['testimonial-img']} ${(i===activeTest)? styles.active:''}`} onClick={()=>updateActive(i)}><img src={item.img} alt="" /></div>
                            )
                        }
                    </div>
                </div>
            </div>
        </>
    );
}

export default Testimonial;
