import React, { useState } from 'react';
import Header from '../header';
import styles from './menu.module.scss';

function NavMenu(props) {
    let [activeMenu, setActiveMenu] = useState(true);
    
    function toggleMenu(){
        setActiveMenu(!activeMenu);
    } 
     
    return (
        <>
            <Header number={props.number} />
            <div className={styles['App-container']}>
                <div className={`${styles['nav-container']} ${(activeMenu)? styles.active:''}`}>
                    <div className={`${styles['nav-list']}`}>
                        <div className={styles['nav-item']}>Home</div>
                        <div className={styles['nav-item']}>Works</div>
                        <div className={styles['nav-item']}>About</div>
                        <div className={styles['nav-item']}>Contact</div>
                    </div>
                    <div class={styles["icon"]} onClick={toggleMenu}>
                        <div class={`${styles.line} ${styles.line1}`}></div>
                        <div class={`${styles.line} ${styles.line2}`}></div>
                    </div>
                </div>
            </div>
        </>
    );
}

export default NavMenu;
