import React, { useState, useEffect } from 'react';
import Header from '../header';
import { IoMdCopy } from "react-icons/io";
import './pass.scss';

function PasswordGen(props) {
    let [password, setPassword] = useState('');
    let [passwordLength, setPasswordLength] = useState(20);
    let [uppercase, setUppercase] = useState(true);
    let [lowercase, setLowercase] = useState(true);
    let [numericals, setNumericals] = useState(true);
    let [symbols, setSymbols] = useState(true);
    let [passwordShow, setPasswordShow] = useState(false);
    function createPassword(){
        // console.log(arr.length,activeSlide);
        let genPassword = '';
        let charset = "";
        if(uppercase){
            charset += "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        }
        if(lowercase){
            charset += "abcdefghijklmnopqrstuvwxyz";
        }
        if(numericals){
            charset += "0123456789";
        }
        if(symbols){
            charset += "!#$%&'()*+,-./:;<=>?@[]^_`{|}~";
        }
        // charset += "abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
        for (var i = 0, n = charset.length; i < passwordLength; ++i) {
            genPassword += charset.charAt(Math.floor(Math.random() * n));
        }
        setPassword(genPassword);
    }
    function updatePasswordLength(e){
        setPasswordLength(e.target.value);
    } 
    function updateUppercase(){
        setUppercase(!uppercase);
    } 
    function updateLowercase(){
        setLowercase(!lowercase);
    } 
    function updateNumericals(){
        setNumericals(!numericals);
    } 
    function updateSymbols(){
        setSymbols(!symbols);
    } 
    function copyPassword(){
        navigator.clipboard.writeText(password);
        setPasswordShow(true);
        setInterval(() => 
            setPasswordShow(false), 3 * 1000
        );
    } 
    useEffect(()=>{   
        createPassword();
    },[passwordLength, lowercase, uppercase, symbols]);  
    return (
        <>
            <Header number={props.number} />
            <div className={`App-container`}>
                <div className="password-generator-container">
                    <h4>Password Generator</h4>
                    <div className="password-field">
                        {password}
                        <div className={`password-copied${(passwordShow)?' show': ''}`}>Password copied to clipboard!</div>
                        <IoMdCopy size={30} onClick={copyPassword}/>
                    </div>
                    <div className="form-setting">
                        <div>Password length</div>
                        <input type="number" min="4" max="20" value={passwordLength} onChange={updatePasswordLength} />
                    </div>
                    <div className="form-setting">
                        <div>Include uppercase letters</div>
                        <input type="checkbox" checked={uppercase} onChange={updateUppercase} />
                    </div>
                    <div className="form-setting">
                        <div>Include lowercase letters</div>
                        <input type="checkbox" checked={lowercase} onChange={updateLowercase} />
                    </div>
                    <div className="form-setting">
                        <div>Include numbers</div>
                        <input type="checkbox" checked={numericals} onChange={updateNumericals} />
                    </div>
                    <div className="form-setting">
                        <div>Include symbols letters</div>
                        <input type="checkbox" checked={symbols} onChange={updateSymbols} />
                    </div>
                </div>
            </div>
        </>
    );
}

export default PasswordGen;
